import React from 'react'
import { Link } from 'react-router-dom'
import Avatar from '@mui/material/Avatar';
import AvatarGroup from '@mui/material/AvatarGroup';


let getTime = (combo) => {
  const upd = new  Date(combo.updated)
  let time = upd.toLocaleTimeString([],{ hour: '2-digit', minute: '2-digit' })
  let date = upd.toLocaleDateString() 
  return time + ' ' + date
}

