// Create API to get proper Bandcamp title and return here instead of a links
let getTitle = (combo) => {
  let title = ''
  for (let value of Object.values(combo.links)) {
    title += value.title.split(' ').slice(0,2).join(' ') + ','
  }
  // console.log(title)
  if (title.length >42) {
    title = title.slice(0,42)
  }
  return title + '...'
}

const ListItem = ({ combo, matches }) => {
  return (
    <div>
        <div>
            <Link to={`/search/${combo.id}`}>
              <div className='combo-list-item'> 
                <h3>{getTitle(combo)}</h3>
                <AvatarGroup max={10} spacing={'small'} style={{width:matches?'92%':'90vw'}}>
                {/* <AvatarGroup max={6} spacing={'small'} style={{width:'90vw'}}> */}
                  {combo.links.map((item, index) => {return (<Avatar key = {index} src={item.img}/>)})}
                </AvatarGroup>
                <p> <span> {getTime(combo)} </span> {Object.keys(combo.links).length}</p>

                
              </div>
             </Link>
        </div>
    </div>
  )
}

export default ListItem
