import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

import  './App.css'
import React from 'react';
import Header from './components/Header'
import ComboListPage from './pages/ComboListPage'
import ComboPage from './pages/ComboPage'
import SearchPage from './pages/SearchPage'
// import { createTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';




import '@fontsource/roboto/300.css';


function App() {
  const matches = useMediaQuery('(min-width:600px)');

  return (
    <Router>
        <div className="container dark">
          <div className="app" style={{height: matches? '92vh':'100vh'}}>
            <Header />
            <Routes>
              <Route exact path="/"  element={<ComboListPage matches={matches}/>} />
              <Route path="/combo/:comboId" element={<ComboPage/>} />
              <Route path="/search"  element={<SearchPage/>} />
              <Route path="/search/:comboId"  element={<SearchPage/>} />
            </Routes>
          </div>
        </div>

    </Router>
  );
}

export default App;
