import { v4 as uuidv4} from 'uuid';

// generating registration automatically
let uuid_user = uuidv4().toString().replaceAll('-','')
let uuid_email = `${uuid_user}@autogen.com`
let password1 = uuidv4()

const user = {
    username: uuid_user,
    password1: password1,
    password2: password1,
    email: uuid_email
};


let getAuthToken = async () => {
  console.log( JSON.stringify(user))
  const response = await fetch("/api/dj-rest-auth/registration/", {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(user)
  });

  if (response.status === 201) {
    console.log('did it!!!')
  } else {
    console.log(response);
  }

  let data = await response.json()
  console.log(data)
  localStorage.clear();
  localStorage.setItem('authToken', data.key);
  return data.key
} 

export default getAuthToken
