import React, {useEffect,useState} from 'react'
import { DataGrid } from '@mui/x-data-grid'
import { Button } from '@mui/material'
import { Link } from 'react-router-dom'
import Avatar from '@mui/material/Avatar';
import AvatarGroup from '@mui/material/AvatarGroup'

const renderDetailsButton = (params) => {
  return (
      <strong>
          <a  href={`${params.value.props.href}` } target={"_blank"}> {params.value.props.href.slice(21,)}
              {/* <Link to={`params.value.props.href`}></Link> */}
          </a>
      </strong>
  )
}



let DynamicGrid = (data) => {
    // const [data, setData] = useState([]);
    // console.log('passed to prop',data.data)

    const columns = [
        { field: 'fan', headerName: 'fan', width: 128, 
            disableClickEventBubbling: true,   renderCell: renderDetailsButton  },
        { field: 'matched', headerName: '#', width: 2 },
        { field: 'tracks_matched', headerName: 'Matched Tracks', width: 550, 
        renderCell: (params) => {
          return (
            <>
              <AvatarGroup> {params.value.avatar.map((item) => {return <Avatar src={item} />})} </AvatarGroup>
            </>
          );
        }
      },
      ];
    
    let rows = null
    if ('results' in data.data) {
    rows = data.data.results.map((row, i) => (
        {
          id: i,
          fan: <a href={row.fan}> {row.fan} </a>,
          matched: row.matched,
          tracks_matched: {avatar:row.tracks_matched}
      }))
    } else {
      console.log('not passed condition')
    }

  return (
          <DataGrid 
            m={2}
            elevation={24}
            rows={rows}
            columns={columns}
            pageSize={4}
            rowsPerPageOptions={[4]}
            autoPageSize={true}
            sx={{ 
              borderColor: 'white',
              bgcolor: 'rgb(37,38,40)',
              '& .MuiDataGrid-cell:hover': {
                color: 'black',
              },
              '& .MuiDataGrid-row:hover': {
                color: 'white',
                bgcolor: 'rgb(221,133,90)' 
              },
              color: 'white'
            }} 
        />

  )
}

export default DynamicGrid
