import React, {useState, useEffect} from 'react'
import ListItem from '../components/ListItem'
import AddButton from '../components/AddButton'
import getAuthToken from '../components/autoLogin'
import SearchButton from '../components/SearchButton'
import { Fade, SpeedDial, SpeedDialAction, SpeedDialIcon
} from '@mui/material'



const ComboListPage = (props) => {
    let [combos, setCombos] = useState([])
    const actions = [
        { icon: <SearchButton />, name: 'Search' },
        { icon: <AddButton />, name: 'Add Bulk Search' },
      ]
    useEffect(() => {
        getCombos()
    }, [])
    
    let getCombos = async () => {
        let token = localStorage.getItem('authToken')
        if (token) {
            token = `Token ${token}` 
        } else {
            try {
                token = getAuthToken()
            } catch (e) {
                console.log(e)
            }

        }

        let dj_r_auth = {
            'Content-Type': 'application/json',
            'Authorization': token
          }

        let fetch_options = { 
            headers: dj_r_auth
        }
        let response = await fetch('/api/combos/', fetch_options)
        let data = await response.json()
        setCombos(data)
        console.log(data)
    }

  return (
    <Fade in={true} {...{ timeout: 500 }}>
        <div className='combos'>
            <div className='combo-header'> 
                {/* UTF-8 Miscellaneous Symbols */}
                <h2 className='combo-title'>&#9838; Searched</h2>
                <p className='combo-count'> {combos?.length}</p>
            </div>

            <div className='combo-list' > 
                {combos.map((combo, index) => (
                    <ListItem key={index} combo={combo} matches={props.matches} />
                ))}
            </div>
                
            <SpeedDial
                ariaLabel="SpeedDial basic example"
                sx={{ position: 'absolute', bottom: 16, right: 16 }}
                icon={<SpeedDialIcon />}
                >
                {actions.map((action) => (
                    <SpeedDialAction
                    key={action.name}
                    icon={action.icon}
                    tooltipTitle={action.name} 
                    />
                ))}
            </SpeedDial>
        </div>
    </Fade>
  )
}

export default ComboListPage

