import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import { useParams, useNavigate } from 'react-router-dom';

import DynamicGrid from '../components/DynamicGrid'
import AllertError from '../components/AllertError';
import ContainerMUI from '../components/ContainerMUI';
import { ReactComponent as ArrowLeft} from '../assets/arrow_left.svg'
import { Button, Slide, Fade, TextField, Box, Switch, Grow, FormControlLabel} from '@mui/material';
import getAuthToken from '../components/autoLogin'
import useMediaQuery from '@mui/material/useMediaQuery';



const ComboPage = ( {} ) => {
    let { comboId } = useParams();
    let navigate = useNavigate();
    let [combo, setCombo] = useState(null)
    const [results, setResults] = useState()
    const [showeditSearch, setShowEditSearch] = useState(true)
    const [showSearchTable, setShowSearchTable] = useState(false)
    const [errorMsg, setErrorMsg] = useState()
    const matches = useMediaQuery('(min-width:600px)');

    let token = localStorage.getItem('authToken')
    token = token? `Token ${token}` : `Token ${getAuthToken()}`
    let dj_r_auth = {
      'Content-Type': 'application/json',
      'Authorization': token
    }

    let getCombo = async () => {      
      if (comboId === 'new') return 
      let response = await fetch(`/api/combo/${comboId}`, {headers: dj_r_auth })
      let data = await response.json()
      setCombo(data)
      
      // this is for search purposes
      let links_to_search = data['links'].map((c,i) => (
        {url: c.link}
      ))
      // callSearch(links_to_search)
    }

    useEffect(() =>{
        getCombo()
    }, [comboId])

    let fillCombo = () => {
      // reformat urls
      let collected_links = combo.links
      if (typeof(results) !== 'undefined') {
        collected_links.map((link,i) => {
          link.title = results.titles[link.link]
          link.img = results.imgs[link.link]
        })
        setCombo({...combo, 'links': collected_links})
      }
    }

    let createSearch = async() => {
      fillCombo()
      fetch(`/api/combo/`, {
        method: 'POST',
        headers: dj_r_auth,
        body: JSON.stringify(combo)
      })
    }


    let updateSearch = async() => {
      fillCombo()
      fetch(`/api/combo/${comboId}`, {
        method: 'PUT',
        headers: dj_r_auth,
        body: JSON.stringify(combo)
      })
    }

    let deleteSearch = async() => {
      fetch(`/api/combo/${comboId}`, {
        method: 'DELETE',
        headers: dj_r_auth,
      })
      .then(res => {
        console.log('deleted page')
        navigate("/", { replace: true })
      })
      
    }

    let handleSubmit = () => {
      if (typeof(combo) === undefined ) {
        // if no state was triggered
        deleteSearch()
      if ( combo.links.length === 0) {
        deleteSearch()
      }
        
      } else if (comboId !== 'new' && combo.links[0]['link'] === '') {
        // if edited but removed all text
        deleteSearch()
      } else if (comboId !== 'new') {
        updateSearch()
      } else if (comboId === 'new' && combo !== null) {
        createSearch()
      }

      setTimeout(function() {
        navigate("/",)
      }, 200)
    }

    // let newSearchCall = () => {
    //   // this is for search purposes
    //   let links_to_search = combo['links'].map((c,i) => (
    //     {url: c.link}
    //   ))
    // }

    let callSearch = async() => {
      setErrorMsg(undefined)    
      setResults(undefined)
      let links_to_search = combo['links'].map((c,i) => (
        {url: c.link}
      ))

      let response = await fetch('/api/search/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(links_to_search)
      })
      if (!response.ok) {
        setErrorMsg('No results')
        console.log('response is not ok')
      } else {
        let data = await response.json()
        setResults(data)
      }
    }

    // const [view, setView] = useState('modify');
    // const handleChange = (event) => {
    //   setView(event.target.value );
    // };

    const [checked, setChecked] = useState(false);
    const handleChangeView = () => {
      setChecked((prev) => !prev);

      // ((comboId === 'new') && 
      if (!checked) {
        if (combo) {
          callSearch()
        } else {
          setErrorMsg('No valid url provided')
        }
      }
    };

    const results_group  = (
      <div style={{ className:'dynamic_results', height: matches? '60vh':'50vh', width: matches? '50vw':'100vw', 
                marginTop:'10px',display: checked?'flex' : 'none',  width:'90%',  
                marginLeft: matches?'5%':'0%', padding:matches?'0%':'2%'}}>
        
      {!errorMsg? 
        results? (<DynamicGrid data={results}/>): null
        :
        <AllertError />
      }
    </div>
    )


  return (
    <div className='combos'>
    <div className='combo-list'>
      <div className='combo-header'> 
          <h3>
            <ArrowLeft onClick={handleSubmit}/> Links
          </h3>
          {(comboId !== 'new')?
          <button onClick={deleteSearch}> Delete </button>
          :
          <button onClick={handleSubmit} > Done </button>
          }
      </div>

      <Box sx={{ width: '95%', height:'100%'}}>
        <FormControlLabel
            control={<Switch checked={checked} onChange={handleChangeView} />} 
            style={{marginLeft:'5%'}} label="Show results vs Edit search"/>

        <Slide direction={'left'} unmountOnExit={true} in={checked} style ={{minHeight:'450px', width:'100%', height:'100%'}} timeout={{ enter: 1000 }}
            {...(checked ? { timeout: 1000 } : {})} 
            mountOnEnter={true}>
            {results_group}
        </Slide>

        <Slide in={!checked} unmountOnExit={true} {...(checked ? { timeout: 1000 } : {})} 
            direction="left" mountOnEnter={true} style={{display: checked? 'none':'block'}}>
            <div style={{width: '90%', marginLeft:'5%'}}>
              {/* Here Call list of titles */
                combo? combo['links'].map((c,i) => (
                  <div className='combo-list-item' key={i}> 
                    {/* temp solution for tracks without titles */}
                    <a key = {i} href={c.link}> {c.title? c.title: c.link} </a>
                  </div>
                  )): null
              }
              <p> Only url's passed through comma are valid</p>
              <Box sx={{width: '95%', m: '2.5%', height: '80%'}} >
                <TextField  
                  value={combo? combo['links'].map((c,i) => c.link.toString()) :'' }
                  color="warning" id="outlined-multiline-static" label="urls" multiline rows={4}
                  sx={{width: '100%',outlineColor: 'white'}}
                  inputProps={{ style: {color: 'white'}}}
                  InputLabelProps={{style: {color: 'white'}}}
                  variant="outlined" size="large"
                  onChange={(e) => {
                    let prop_format = []
                    const arr_links = e.target.value.split(',')
                    arr_links.map( (c,i) => {prop_format.push({'link': c}) })
                    setCombo({...combo, 'links': prop_format})
                    } }>
                </TextField>
              </Box>
            </div>
         </Slide>

          {(checked && !(results || errorMsg))? <ContainerMUI />  : null}

      </Box>
    </div>
    </div>
  )
}





export default ComboPage
