import * as React from 'react'

import { useParams, useNavigate } from 'react-router-dom';
import DynamicGrid from '../components/DynamicGrid'
import Fade from '@mui/material/Fade';
import { Slide, TextField } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';

import AddCircleOutlineOutlinedIcon from '@mui/icons-material/AddCircleOutlineOutlined';
import SaveIcon from '@mui/icons-material/Save';
import SendIcon from '@mui/icons-material/Send';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';

import Snackbar from '@mui/material/Snackbar';
import CloseIcon from '@mui/icons-material/Close';

import AllertError from '../components/AllertError';
import ContainerMUI from '../components/ContainerMUI';
import getAuthToken from '../components/autoLogin'
import Avatar from '@mui/material/Avatar';
import Stack from '@mui/material/Stack';
import Skeleton from '@mui/material/Skeleton'
import useMediaQuery from '@mui/material/useMediaQuery';








const SearchPage = ( {} ) => {
  let [loading, setLoading] = React.useState(false)
  const [results, setResults] = React.useState()
  const [errorMsg, setErrorMsg] = React.useState()
  const [formFields, setFormFields] = React.useState([ { url: ''} ])
  const [savedSearch, setSavedSearch] = React.useState()
  const [open, setOpen] = React.useState(false);
  let navigate = useNavigate()
  let { comboId } = useParams()
  const matches = useMediaQuery('(min-width:600px)')
  const [checked, setChecked] = React.useState(false)

  const myRef = React.useRef(null)
  
  let token = localStorage.getItem('authToken')
    token = token? `Token ${token}` : `Token ${getAuthToken()}`
    let dj_r_auth = {
      'Content-Type': 'application/json',
      'Authorization': token
    }


    let getSavedSearch = async () => {      
      if (comboId === undefined) return 
      let response = await fetch(`/api/combo/${comboId}`, {headers: dj_r_auth })
      let data = await response.json()
      setSavedSearch(data)
      
      // this is for search purposes
      let links_to_search = data['links'].map((c,i) => (
        {url: c.link, title: c.title, img:c.img}
      ))
      // console.log(data)
      console.log('links to search original',links_to_search )
      
      setFormFields(links_to_search)
    }

    React.useEffect(() =>{
      getSavedSearch()
    }, [comboId]) 



  const handleFormChange = (event, index) => {
    let data = [...formFields];
    data[index][event.target.name] = event.target.value;
    setFormFields(data);
  }
  const handleToClose = (event, reason) => {
    if ("clickaway" == reason) return;
    setOpen(false);
  }; 

  const snackAction = (
                      <React.Fragment>
                        <IconButton size="small" aria-label="close" color="inherit" onClick={handleToClose}>
                          <CloseIcon fontSize="small" />
                        </IconButton>
                      </React.Fragment>
  )

  const onSubmit = (e) => {
    setChecked(false)
    e.preventDefault()
    setLoading(true)
    setErrorMsg(undefined) 
    setResults(undefined) 
    callSearch(formFields)
  }


  const onSave = (e) => {
    if (results) {
      let format_query_array = []
      Object.entries(results['titles']).forEach(([key, value]) => {
        format_query_array.push({link: key, title:value, img:results['imgs'][key]})
     });
      let format_query_obj = {links: format_query_array}
      fetch(`/api/combo/`, {
        method: 'POST',
        headers: dj_r_auth,
        body: JSON.stringify(format_query_obj)
      })
      setOpen(true);
    } else{

      alert('Search for the query and then press SAVE')
    }
  }
  const deleteSearch = async() => {
    fetch(`/api/combo/${comboId}`, {
      method: 'DELETE',
      headers: dj_r_auth,
    })
    .then(res => {
      console.log('deleted page')
      navigate("/", { replace: true })
    })
  }
  const executeScroll = () => myRef.current.scrollIntoView({behavior: "smooth", block: "end", inline: "nearest"})
  let callSearch = async(formFields) => {
    let response = await fetch('/api/search/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(formFields)
    })
    if (!response.ok) {
      setErrorMsg('No results')
      console.log('response is not ok')
      setLoading(false)
    } else {
      let data = await response.json()
      setResults(data)

      // reform resutls and update formFields to read new titles and imgs
      let format_query_array = []
      Object.entries(data['titles']).forEach(([key, value]) => {
        format_query_array.push({url: key, title:value, img:data['imgs'][key]})
     });
      console.log(format_query_array)
      setFormFields(format_query_array)
      // console.log('call Search',data) 

    }

    setLoading(false)
    setChecked(true)
    executeScroll()
  }


  const addFields = () => {
    let object = {url: ''}
    setFormFields([...formFields, object])
  }

  const removeFields = (index) => {
    let data = [...formFields];
    data.splice(index, 1)
    setFormFields(data)
  }



  return (
    <div className='combos'>
    <div className='combo-list'>
      <Fade in={true} sx = {{width: '95%', height:'100vh'}} {...{ timeout: 500 }}>
        

          <div className='combos' key='combos' >
          {loading? <ContainerMUI/>: null}
            <div className='combo-header'> 
                {/* UTF-8 Miscellaneous Symbols */}
                <h2 className='combo-title'>&#9838; Search </h2>
                {comboId? 
                  <Button size="small"  onClick={deleteSearch}  color={'error'} variant="contained" endIcon= {<DeleteIcon/>} >  Delete </Button>
                :null}
                <Button size="small" onClick={onSave}  color={'success'} variant="contained" endIcon= {<SaveIcon/>} >  Save</Button>
                <Snackbar
                  anchorOrigin={{ vertical: 'top', horizontal: 'center', }}
                  open={open}
                  autoHideDuration={5000}
                  message="Search links saved"
                  onClose={handleToClose}
                  action={snackAction}
                /> 

                
            </div>
            <Box  noValidate autoComplete="off" 
                sx={{ marginLeft: '15%',
                  '& .MuiTextField-root': { m: 2 , height: '80%', width: '100%'},
                }}>
                

              {/* <div className='search' style={{ bgcolor: 'rgb(37,38,40)'}} >  */}
              <div > 
                  <form onSubmit={onSubmit} >
                  {formFields.map((form, index) => {
                    return (
                      <Fade in={true} sx = {{width: '95%'}} {...{ timeout: 500 }}>
                        <Stack direction="row" alignItems="center" spacing={2} key={index} style ={{marginLeft: '-20px', }}>
                          {form.img? <a href={form.url} target={"_blank"}><Avatar alt="release img" variant={'square'} src={form.img} sx={{ width: 56, height: 56 }} /> </a>: <Skeleton animation={false} variant ='circular'> <Avatar sx={{ width: 56, height: 56}}  /> </Skeleton>}
                          <TextField 
                            id="outlined-basic" label={form.title? form.title: "URL..."} variant="outlined"
                            style={{width: '80%'}}
                            color={'warning'}
                            inputProps={{ style: {color: 'white'}}}
                            InputLabelProps={{style: {color: 'white', borderColor:'white'}}}
                            name='url'
                            placeholder='Bandcamp track/album url here...'
                            onChange={event => handleFormChange(event, index)}
                            value={form.url}
                          />
                          <IconButton color={'warning'} onClick={() => removeFields(index)}><DeleteIcon /></IconButton>
                        </Stack>
                        
                      </Fade>

                    )
                  })}
                </form>
                <IconButton onClick={addFields} color={'warning'}> <AddCircleOutlineOutlinedIcon /> </IconButton>
                <br></br><br></br>
                <Button onClick={onSubmit}  color={'warning'} variant="contained" endIcon= {<SendIcon/>} 
                  style={{marginTop:'-5vh'}} > Search links </Button>
              </div>
              </Box>
        </div>
      </Fade>
      <div ref={myRef} >
        <Fade in={checked} {...(checked ? { timeout: 2000 } : {})} >
          <div key= {1} style={{ height: matches? '60vh':'50vh', width: matches? '80%':'100vw', 
            marginTop:'10px', 
            marginLeft: matches?'12.5%':'0%', padding:matches?'0%':'2%'}}>
              {
                errorMsg? <AllertError/>:
                results? (<DynamicGrid data={results}/>): null
              }
          </div>
        </Fade >
        </div>
    </div>
    </div>
  )
}

export default SearchPage
