import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';

import {Popover, Typography, Fade} from '@mui/material'
// import yashch11 from '../assets/yashch11.mp4'
import yashch11 from '../assets/yashch11.gif'


export default function SimpleBackdrop() {
  const [open, setOpen] = React.useState(false);
  
  const [anchorEl, setAnchorEl] = React.useState(null);
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };
  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };


  const openH = Boolean(anchorEl);



  return (
    <div>
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={true}
        onClick={handleClose}  timeout={{ enter: 2000 }}
      >
        {/* <CircularProgress color="inherit" /> */}
            <img src={yashch11} alt={'Loading reptile run'} style={{maxWidth: '42%'}} onMouseEnter={handlePopoverOpen} onMouseLeave={handlePopoverClose} />
            <Popover
              id="mouse-over-popover"
              sx={{
                pointerEvents: 'none',
              }}
              open={openH}
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'center',
              }}
              onClose={handlePopoverClose}
              disableRestoreFocus
            >
              <Typography backgroundColor='primary.dark' sx={{ p: 1 }}>Lizzards runs to search trax.</Typography>
            </Popover>

      </Backdrop>
    </div>

  );
}
